	import uniSwiperDot from "@/components/uni-swiper-dot/uni-swiper-dot.vue"
	export default {
		data() {
			return {
                current: 0,
				tabIndex: 0,
				changeIndex: 1,
				mode: 'long&nav',
				itemId: null,
				changeList: [],
				dotsStyles: {
					backgroundColor: 'rgba(0, 0, 0,.3)',
					color: '#fff',
					selectedBackgroundColor: 'rgba(255, 255, 255, .9)'
				}
			}
		},
		props:{
			info: {
			    type: Array,
			    default() {
			        return [];
			    }
			},
			module: {
			    type: Array,
			    default() {
			        return [];
			    }
			},
			more: {
			    type: Array,
			    default() {
			        return [];
			    }
			},
			flag: {
			    type: Boolean,
			    default() {
			        return false;
			    }
			},
			star: {
			    type: Array,
			    default() {
			        return [];
			    }
			},
            actor: {
                type: Array,
                default() {
                    return [];
                }
            },
            indexTab: {
                type: Number,
                default() {
                    return 0;
                }
            }
		},
        watch: {
            info(newValue, oldValue) {
                if (newValue.length < oldValue.length && this.current == oldValue.length - 1) {
                    this.current = newValue.length - 1;
                }
            }
        },
		components:{
			uniSwiperDot
		},
		methods: {
			change(e) {
				this.current = e.detail.current;
			},
			changeStar(index,id){
				this.tabIndex = index;
				this.$emit('star',{index,id})
			},
			changeItems(id){
				this.changeIndex++;
				if (this.changeIndex <= 3) {
					this.itemId = id;
				} else{
					this.itemId = null;
					this.changeIndex = 1;
                    this.changeList = [];
					return;
				}
				uni.request({
					url: this.websiteUrl + '/api/home/exchange',
					method: 'GET',
					data: {
                        typeId: this.indexTab,
                        modleId: id,
                        currPage: this.changeIndex,
                    },
					success: res => {
						this.changeList = res.data;
					},
					fail: () => {},
					complete: () => {}
				});
			},
            navigateTo(name,id){
                uni.navigateTo({
                    url: '/pages/more/more?title='+name+'&index='+this.indexTab+'&id='+id,
                    animationType: 'slide-in-right',
                    animationDuration: 300
                });
            },
            movieTo(e){
                let movieId = e.currentTarget.dataset.id;
                uni.navigateTo({
                    url: '/pages/playPage/playPage?vodId='+movieId,
                    animationType: 'slide-in-right',
                    animationDuration: 300
                });
            }
		}
	}
